import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { Routes, RouterModule } from '@angular/router';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PickListComponent } from './pick-list.component';
import { PickListService } from 'src/app/service/pick-list.service';

const routes: Routes = [
  {
    path: '',
    component: PickListComponent,
    data: { title: 'pick_list_list' },
  },
  {
    path: 'generate',
    loadChildren: () => import('./generate/generate.module').then(m => m.GenerateModule),
    data: { title: 'add_pick_list' },
  },
  {
    path: 'edit',
    loadChildren: () => import('./generate/generate.module').then(m => m.GenerateModule),
    data: { title: 'edit_pick_list' },
  },
  {
    path: 'view',
    loadChildren: () => import('./pick-list-view/pick-list-view.module').then(m => m.PickListViewModule),
    data: { title: 'view_pick_list' },
  },
  {
    path: 'generate-pick-list',
    loadChildren: () => import('./generate-pick-list/generate-pick-list.module').then(m => m.GeneratePickListModule),
    data: { title: 'add_pick_list' },
  },

]

@NgModule({
  declarations: [PickListComponent],
  imports: [
    CommonModule,
    SearchModule,
    BtnAddModule,
    PaginationModule,
    TableListModule,
    RouterModule.forChild(routes)
  ], providers: [PickListService]
})
export class PickListModule { }
