import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField, DateField } from 'src/app/common-module/table-list/list-field-type';
import { PickListService } from 'src/app/service/pick-list.service';

@Component({
  selector: 'app-pick-list',
  templateUrl: './pick-list.component.html'
})
export class PickListComponent implements OnInit {

  PageTitle = "Manage PickList";

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private picklistService: PickListService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Order No",
        key: "sales_order.sales_order_no",
      }),
      new TextField({
        label: "Client Name",
        key: "client.label",
      }),

      new DateField({
        label: "Order Date",
        key: "sales_order.sales_order_date",
      }),
      new DateField({
        label: "PickList Date",
        key: "pick_list_date",
      }),
      new TextField({
        label: "Delievery Name",
        key: "delivery_name",
      }),
      new TextField({
        label: "Receipt Name",
        key: "receiver_name",
      }),
    );

    let canEdit = true;
    let canView = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/outbound/pick-list/edit/',
        id: 'pick_list_id'
      }
    }
    if (canView) {
      actions['view'] = {
        path: '/outbound/pick-list/view/',
        id: 'pick_list_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'picking/',
        id: 'pick_list_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.picklistService.getPickList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list ? response.data.list : [];
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}
